html, body {
  height: 100%;
  width: 100%;
  -webkit-font-smoothing: antialiased;
}

html {
  //overflow-y: scroll;
  min-width: 1440px;
  max-width: 1920px;
  //min-height: 768px;
  background-color: #ebebeb;
}

body {
  display: block;
  position: relative;
}

html, body, div, header,
nav, aside, article, section,
footer, p, span, h1,
h2, h3, h4, h5, h6,
figure, figcaption, fieldset,
input, button, ul, li,
label {
  margin: 0;
  padding: 0;
  outline: none;
  border: 0;
  vertical-align: baseline;
  font-size: 100%;
  font-weight: 400;
}

a, a:visited, a:active {
  text-decoration: none;
  outline: none;
  color: inherit;
}

article, aside, details, figcaption, figure, footer, header, menu, nav, section {
  display: block;
}

#content_wrapper {
  width: 90%;
  margin: 0 auto;
  position: relative;
  //overflow-x: hidden;
  font-family: 'Roboto', 'Arial', sans-serif;
}

#main_header {
  position: relative;
  width: 100%;
  height: 95px;
  background-color: #fff;
  font-family: 'Roboto', 'Arial', sans-serif;
  font-weight: normal;
  font-style: normal;
  margin-bottom: 60px;
  img {
    float: left;
    padding: 5px 30px;
  }

  a#user_avatar {
    position: absolute;
    top: 10px;
    right: 30px;
    width: 75px;
    height: 75px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    img {
      margin: 0;
      padding: 0;
      width: 75px;
      height: 75px;
      -webkit-border-radius: 50%;
      -moz-border-radius: 50%;
      border-radius: 50%;
    }
  }
}

#main_menu {
  position: relative;
  > a,
  .subnav > a {
    display: block;
    padding: 0;
    float: left;
    color: #2b2b2b;
    margin: 35px 10px;
    font-size: 25px;
    line-height: 25px;
  }
  .subnav-content > a {
    display: block;
    margin: 0 10px;
    padding: 0;
    float: left;
    color: #2b2b2b;
    font-size: 20px;
    line-height: 20px;
  }
  a:hover,
  a.active {
    color: #eb5252;
  }
}

.subnav {
  float: left;
  overflow: hidden;
  padding-bottom: 10px;
}

.subnav-content {
  display: none;
  position: absolute;
  left: 0;
  top: 105px;
  width: calc(100% - 40px);
  background-color: #fff;
  padding: 10px 20px;
}
div.subnav .active {
  display: block;
}

#side_menu {
  position: relative;
  width: 420px;
  background-color: #fff;
  margin-top: 30px;
  float: left;
  text-align: center;
  padding: 25px 0;
  .side_img_link {
    margin: 5px 0;
  }

  #notice {
    display: flex;
    width: 365px;
    height: 500px;
    background-color: #eb5252;
    color: #fff;
    margin: 5px auto;
    border-radius: 5px;
    align-items: center;
    justify-content: center;

    p {
      font-size: 30px;
      line-height: 30px;
    }
  }
}

#main_content {
  position: relative;
  width: calc(100% - 450px);
  background-color: #fff;
  margin-top: 30px;
  margin-left: 30px;
  float: right;
}

#main_footer {
  width: 100%;
  height: 300px;
  background-color: #b34050;
  margin-top: 30px;

  #copyrights {
    position: absolute;
    width: 100%;
    height: 40px;
    line-height: 40px;
    bottom: 0;
    text-align: center;
    color: #fff;
    font-size: 15px;
    background-color: #9b303f;
  }
}
